.services {
  background-color: $background;
}

.services__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  padding-top: 93px;
  box-sizing: border-box;

  @media (max-width: 1023px) {
    padding: 52px 28px 0 25px;
  }

  @media (max-width: 767px) {
    padding: 53px 15px 0;
  }
}

.services__wrapper p {
  margin: 0;
  margin-bottom: 47px;
  font-size: 36px;
  line-height: 51px;

  @media (max-width: 1023px) {
    margin-right: 100px;
    font-size: 32px;
    line-height: 45px;
  }

  @media (max-width: 767px) {
    margin-bottom: 40px;
    margin-right: 0;
    margin-left: 10px;
    font-size: 28px;
    line-height: 39px;
  }
}

.services__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  padding-bottom: 97px;
  list-style: none;

  @media (max-width: 1023px) {
    flex-direction: column;
    align-items: center;
    padding-bottom: 32px;
  }
}

.services__item a {
  position: relative;
}

.services__item {
  position: relative;
  box-sizing: border-box;
  margin-bottom: 30px;

  @media (max-width: 1200px) {
    width: calc((100% - 90px)/3);
  }

  @media (max-width: 1023px) {
    width: min-content;
    margin-bottom: 46px;
  }

  @media (max-width: 767px) {
    margin-bottom: 30px;
  }

  &:hover {
    outline: 3px solid $blue;
  }

  &::after {
    position: absolute;
    content: "";
    bottom: 0;
    right: 0;
    width: 63px;
    height: 63px;
    background-color: $blue;
    background-image: url("../../img/icon/pointer.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    z-index: 5;
  }
}

.services__item p {
  position: absolute;
  width: 160px;
  margin: 0;
  padding-top: 53px;
  padding-left: 44px;
  font-size: 20px;
  line-height: 30px;
  color: $white;

  @media (max-width: 1023px) {
    padding-top: 43px;
  }

  @media (max-width: 767px) {
    padding-top: 23px;
    padding-left: 35px;
  }
}

.services__item:last-child p {
  @media (max-width: 1023px) {
    padding-top: 53px;
  }

  @media (max-width: 1023px) {
    padding-top: 30px;
  }
}

.services__item img {
  display: block;
  object-fit: cover;
  opacity: 0.25;

  @media (max-width: 1200px) {
    width: 100%;
  }

  @media (max-width: 1023px) {
    width: 715px;
    height: 256px;
  }

  @media (max-width: 767px) {
    width: 290px;
  }
}
