.about {
  color: $dark-grey;
}

.about__wrapper {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 194px;
  padding-right: 20px;
  padding-top: 80px;
  box-sizing: border-box;

  @media (max-width: 1023px) {
    padding: 25px 20px 0 210px;
  }

  @media (max-width: 767px) {
    padding: 35px 15px 0;
  }

  &::before {
    position: absolute;
    content: "";
    width: 220px;
    height: 317px;
    left: -15px;
    background-image: url("../img/year.png");
    background-repeat: no-repeat;
    background-position: 50% 50%;

    @media (max-width: 1023px) {
      left: -3px;
      width: 251px;
      height: 362px;
      background-image: url("../img/year_tablet.png");
    }

    @media (max-width: 767px) {
      display: none;
    }
  }
}

.about__wrapper h2 {
  margin: 0;
  margin-bottom: 18px;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;

  @media (max-width: 1023px) {
    margin-bottom: 25px;
    font-size: 32px;
    line-height: 45px;
  }

  @media (max-width: 767px) {
    margin-bottom: 23px;
    font-size: 28px;
    line-height: 39px;
  }
}

.about__wrapper p {
  margin: 0;
  margin-bottom: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;

  @media (max-width: 1023px) {
    padding-left: 3px;
    margin-bottom: 21px;
  }

  @media (max-width: 767px) {
    padding-left: 0;
    margin-bottom: 41px;
  }
}

.about__wrapper p:last-of-type {
  @media (max-width: 767px) {
    margin-bottom: 31px;
  }
}

.about__button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 107px;
  padding: 14px 50px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $blue;
  background-color: transparent;
  border: 1px solid $blue;
  border-radius: 5px;
  cursor: pointer;

  @media (max-width: 1023px) {
    margin-top: 20px;
    margin-bottom: 83px;
  }

  @media (max-width: 767px) {
    margin-bottom: 65px;
  }

  &:hover {
    color: $white;
    background-color: $blue;
  }

  &:active {
    opacity: 0.5;
  }
}

.about__long-text {
  @media (max-width: 1023px) {
    display: none;
  }
}

.about__tablet-hidden {
  @media (min-width: 1024px) {
    display: none;
  }
}
